import {
  Button,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerOverlay,
  Flex,
  Text,
  useDisclosure,
  VStack,
} from "@chakra-ui/react";
import React from "react";
import { TbMenu } from "react-icons/tb";
import { navlinks } from "../../constants/navlinks";
import { Link as ScrollLink } from "react-scroll";

const NavbarMenuBtn = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  return (
    <>
      <Button
        ref={btnRef}
        onClick={onOpen}
        px={0}
        py={0}
        h="40px"
        w="40px"
        bg="#fff"
        rounded="full"
      >
        <TbMenu fontSize="20px" color="#001E3C" />
      </Button>
      <Drawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
        finalFocusRef={btnRef}
        size="full"
      >
        <DrawerOverlay />
        <DrawerContent bg="bgPrimary">
          <DrawerCloseButton fontSize="20px" p={5} />
          <DrawerBody>
            <Flex h="100%" w="100%" justifyContent="center" alignItems="center">
              <VStack gap={3} marginBottom={7}>
                {navlinks.map((link, i) => (
                  <ScrollLink
                    key={i}
                    to={link}
                    spy={true}
                    smooth={true}
                    offset={-100}
                    duration={500}
                    onClick={onClose}
                  >
                    <Text
                      textTransform="uppercase"
                      fontSize="2xl"
                      opacity="0.6"
                      fontWeight="500"
                      cursor="pointer"
                      _hover={{ opacity: 1 }}
                    >
                      {link}
                    </Text>
                  </ScrollLink>
                ))}
                <Text
                  textTransform="uppercase"
                  fontSize="2xl"
                  opacity="0.6"
                  fontWeight="500"
                  cursor="pointer"
                  _hover={{ opacity: 1 }}
                  onClick={onClose}
                >
                  Resume
                </Text>
              </VStack>
            </Flex>
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </>
  );
};

export default NavbarMenuBtn;
