import { Box, Button, Container, Flex, Hide, Text } from "@chakra-ui/react";
import { navlinks } from "../../constants/navlinks";
import { AiFillGithub, AiFillLinkedin, AiOutlineMail } from "react-icons/ai";
import React, { useEffect, useState } from "react";
import { chakra, shouldForwardProp } from "@chakra-ui/react";
import { motion, isValidMotionProp } from "framer-motion";
import NavbarMenuBtn from "./NavbarMenuBtn";
import { Link as ScrollLink } from "react-scroll";

const ChakraBox = chakra(motion.div, {
  /**
   * Allow motion props and non-Chakra props to be forwarded.
   */
  shouldForwardProp: (prop) =>
    isValidMotionProp(prop) || shouldForwardProp(prop),
});

const Navbar = () => {
  const [scroll, setScroll] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      setScroll(window.scrollY > 20);
    });
  }, []);

  return (
    <ChakraBox
      initial={{ y: -25 }}
      animate={{ y: 0 }}
      transition={{ duration: 0.5 }}
      h="{20}"
      zIndex="100"
      position="sticky"
      top={"0"}
      style={
        scroll
          ? {
              background: "rgba(10, 25, 41, 0.70)",
              backdropFilter: "blur(10px)",
            }
          : null
      }
    >
      <Container maxW="7xl" h={20}>
        <Flex justify="space-between" h={20} alignItems="center">
          <Flex
            justifyContent="center"
            alignItems="center"
            bg="#FFFFFF"
            color="bgPrimary"
            fontWeight="600"
            fontSize={20}
            h="40px"
            w="40px"
            rounded="full"
            cursor="pointer"
          >
            H
          </Flex>
          <Hide below="md">
            <Flex justifyContent="space-between" paddingLeft="50px">
              {navlinks.map((link, i) => (
                <ScrollLink
                  key={i}
                  to={link}
                  spy={true}
                  smooth={true}
                  offset={-100}
                  duration={500}
                >
                  <Text
                    textTransform="uppercase"
                    opacity={0.6}
                    fontSize="sm"
                    cursor="pointer"
                    mx={4}
                    _hover={{ opacity: 1 }}
                  >
                    {link}
                  </Text>
                </ScrollLink>
              ))}
              <Text
                textTransform="uppercase"
                opacity={0.6}
                fontSize="sm"
                cursor="pointer"
                mx={4}
                _hover={{ opacity: 1 }}
              >
                Resume
              </Text>
            </Flex>
            <Flex>
              <Box
                opacity={0.6}
                _hover={{ opacity: 1 }}
                cursor="pointer"
                mx="6px"
              >
                <AiFillGithub fontSize="22px" />
              </Box>
              <Box
                opacity={0.6}
                _hover={{ opacity: 1 }}
                cursor="pointer"
                mx="6px"
              >
                <AiFillLinkedin fontSize="22px" />
              </Box>
              <Box
                opacity={0.6}
                _hover={{ opacity: 1 }}
                cursor="pointer"
                mx="6px"
              >
                <AiOutlineMail fontSize="22px" />
              </Box>
            </Flex>
          </Hide>
          <Hide above="md">
            <NavbarMenuBtn />
          </Hide>
        </Flex>
      </Container>
    </ChakraBox>
  );
};

export default Navbar;
